'use client'

import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import Link from 'next/link'

interface Assessment {
  id: string
  name: string
  description: string
  duration: string
  questions: number
  xpReward: number
  difficulty: 'Beginner' | 'Intermediate' | 'Advanced'
  completed: boolean
  completionDate?: string
  score?: number
}

export default function AssessmentsPage() {
  const assessments: Assessment[] = [
    {
      id: 'riasec',
      name: 'RIASEC Career Interest Test',
      description: 'Discover your career interests based on the Holland Code theory. This assessment helps identify which work environments and career paths align with your personality and interests.',
      duration: '15-20 minutes',
      questions: 60,
      xpReward: 200,
      difficulty: 'Beginner',
      completed: true,
      completionDate: '2024-01-15',
      score: 85
    },
    {
      id: 'mbti',
      name: 'MBTI Personality Assessment',
      description: 'Understand your personality type using the Myers-Briggs Type Indicator framework. Learn about your preferences in how you perceive the world and make decisions.',
      duration: '20-25 minutes',
      questions: 80,
      xpReward: 250,
      difficulty: 'Intermediate',
      completed: true,
      completionDate: '2024-01-18',
      score: 92
    },
    {
      id: 'skills',
      name: 'Skills Aptitude Test',
      description: 'Evaluate your technical and soft skills across various domains. Get insights into your strengths and areas for improvement to guide your career development.',
      duration: '30-35 minutes',
      questions: 100,
      xpReward: 300,
      difficulty: 'Advanced',
      completed: false
    },
    {
      id: 'emotional-intelligence',
      name: 'Emotional Intelligence Assessment',
      description: 'Measure your ability to understand and manage emotions, both your own and others. Essential for leadership and interpersonal effectiveness.',
      duration: '15-20 minutes',
      questions: 50,
      xpReward: 180,
      difficulty: 'Intermediate',
      completed: false
    },
    {
      id: 'leadership-style',
      name: 'Leadership Style Inventory',
      description: 'Identify your natural leadership approach and learn how to adapt your style to different situations and team dynamics.',
      duration: '20-25 minutes',
      questions: 70,
      xpReward: 220,
      difficulty: 'Advanced',
      completed: false
    },
    {
      id: 'work-values',
      name: 'Work Values Assessment',
      description: 'Understand what motivates you at work and what type of work environment will make you most satisfied and productive.',
      duration: '10-15 minutes',
      questions: 40,
      xpReward: 150,
      difficulty: 'Beginner',
      completed: false
    }
  ]

  const completedAssessments = assessments.filter(a => a.completed)
  const availableAssessments = assessments.filter(a => !a.completed)
  const totalXPEarned = completedAssessments.reduce((sum, a) => sum + a.xpReward, 0)
  const totalPossibleXP = assessments.reduce((sum, a) => sum + a.xpReward, 0)

  const getDifficultyColor = (difficulty: Assessment['difficulty']) => {
    switch (difficulty) {
      case 'Beginner': return 'bg-green-500'
      case 'Intermediate': return 'bg-yellow-500'
      case 'Advanced': return 'bg-red-500'
      default: return 'bg-gray-500'
    }
  }

  return (
    <div className="max-w-6xl mx-auto">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-foreground mb-2">Career Assessments</h1>
        <p className="text-muted-foreground">
          Take comprehensive assessments to discover your career path and unlock personalized recommendations
        </p>
      </div>

      {/* Progress Overview */}
      <Card className="mb-8">
        <CardHeader>
          <CardTitle>Your Assessment Progress</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-6">
            <div className="text-center">
              <div className="text-3xl font-bold text-primary mb-1">{completedAssessments.length}</div>
              <div className="text-sm text-muted-foreground">Completed</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-primary mb-1">{availableAssessments.length}</div>
              <div className="text-sm text-muted-foreground">Available</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-primary mb-1">{totalXPEarned}</div>
              <div className="text-sm text-muted-foreground">XP Earned</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-primary mb-1">
                {completedAssessments.length > 0 
                  ? Math.round(completedAssessments.reduce((sum, a) => sum + (a.score || 0), 0) / completedAssessments.length)
                  : 0}%
              </div>
              <div className="text-sm text-muted-foreground">Avg Score</div>
            </div>
          </div>
          
          <div>
            <div className="flex justify-between text-sm mb-2">
              <span>Overall Progress</span>
              <span>{completedAssessments.length} / {assessments.length} assessments</span>
            </div>
            <Progress value={(completedAssessments.length / assessments.length) * 100} />
          </div>
        </CardContent>
      </Card>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        {/* Available Assessments */}
        <div>
          <h2 className="text-2xl font-semibold text-foreground mb-6">Available Assessments</h2>
          <div className="space-y-6">
            {availableAssessments.map((assessment) => (
              <Card key={assessment.id} className="hover:shadow-md transition-shadow">
                <CardHeader>
                  <div className="flex items-center justify-between">
                    <CardTitle className="text-lg">{assessment.name}</CardTitle>
                    <div className="flex items-center space-x-2">
                      <div className={`w-3 h-3 rounded-full ${getDifficultyColor(assessment.difficulty)}`}></div>
                      <Badge variant="outline" className="text-xs">
                        {assessment.difficulty}
                      </Badge>
                    </div>
                  </div>
                </CardHeader>
                <CardContent className="space-y-4">
                  <p className="text-muted-foreground text-sm">{assessment.description}</p>
                  
                  <div className="grid grid-cols-2 gap-4 text-sm">
                    <div>
                      <span className="font-medium text-foreground">Duration:</span>
                      <p className="text-muted-foreground">{assessment.duration}</p>
                    </div>
                    <div>
                      <span className="font-medium text-foreground">Questions:</span>
                      <p className="text-muted-foreground">{assessment.questions}</p>
                    </div>
                  </div>

                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-2">
                      <span className="text-sm font-medium text-foreground">Reward:</span>
                      <Badge variant="secondary">+{assessment.xpReward} XP</Badge>
                    </div>
                    <Link href={`/assessments/${assessment.id}`}>
                      <Button>Start Assessment</Button>
                    </Link>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>

        {/* Completed Assessments */}
        <div>
          <h2 className="text-2xl font-semibold text-foreground mb-6">Completed Assessments</h2>
          {completedAssessments.length === 0 ? (
            <Card>
              <CardContent className="pt-6 text-center">
                <p className="text-muted-foreground mb-4">No assessments completed yet</p>
                <p className="text-sm text-muted-foreground">
                  Complete your first assessment to get personalized career recommendations
                </p>
              </CardContent>
            </Card>
          ) : (
            <div className="space-y-6">
              {completedAssessments.map((assessment) => (
                <Card key={assessment.id} className="border-green-200 bg-green-50/50 dark:border-green-800 dark:bg-green-950/20">
                  <CardHeader>
                    <div className="flex items-center justify-between">
                      <CardTitle className="text-lg">{assessment.name}</CardTitle>
                      <div className="flex items-center space-x-2">
                        <Badge variant="secondary" className="bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200">
                          ✓ Completed
                        </Badge>
                        <Badge variant="outline">
                          {assessment.score}%
                        </Badge>
                      </div>
                    </div>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <p className="text-muted-foreground text-sm">{assessment.description}</p>
                    
                    <div className="grid grid-cols-2 gap-4 text-sm">
                      <div>
                        <span className="font-medium text-foreground">Completed:</span>
                        <p className="text-muted-foreground">
                          {assessment.completionDate ? new Date(assessment.completionDate).toLocaleDateString() : 'N/A'}
                        </p>
                      </div>
                      <div>
                        <span className="font-medium text-foreground">XP Earned:</span>
                        <p className="text-muted-foreground">+{assessment.xpReward} XP</p>
                      </div>
                    </div>

                    <div className="flex items-center justify-between">
                      <div className="flex items-center space-x-2">
                        <span className="text-sm font-medium text-foreground">Score:</span>
                        <div className="flex items-center space-x-2">
                          <Progress value={assessment.score || 0} className="w-20" />
                          <span className="text-sm text-muted-foreground">{assessment.score}%</span>
                        </div>
                      </div>
                      <div className="flex space-x-2">
                        <Button variant="outline" size="sm">
                          View Results
                        </Button>
                        <Link href={`/assessments/${assessment.id}`}>
                          <Button variant="outline" size="sm">
                            Retake
                          </Button>
                        </Link>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          )}
        </div>
      </div>

      {/* Call to Action */}
      <Card className="mt-8 bg-gradient-to-r from-primary/10 to-primary/5">
        <CardContent className="pt-6 text-center">
          <h3 className="text-xl font-semibold text-foreground mb-2">
            Ready to discover your ideal career path?
          </h3>
          <p className="text-muted-foreground mb-6">
            Complete all assessments to unlock advanced AI-powered career recommendations and personalized learning paths.
          </p>
          <div className="flex justify-center space-x-4">
            {availableAssessments.length > 0 && (
              <Link href={`/assessments/${availableAssessments[0].id}`}>
                <Button size="lg">
                  Start Next Assessment
                </Button>
              </Link>
            )}
            <Button variant="outline" size="lg">
              View Career Recommendations
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
